<template>
  <v-app>
    <v-app toolbar footer>

      <v-navigation-drawer 
      dark
      temporary
      
      v-model="drawer"
      enable-resize-watcher
      >

        <v-toolbar flat class="transparent">
          <v-list class="pt-0">
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <img src="https://qph.ec.quoracdn.net/main-qimg-d5aa790ac72d8bf10643e2139624f16c" />
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>Edu Cordova</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
